import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { Button, CircularProgress, Snackbar, AppBar, Toolbar, Typography, Grid} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import TwitterIcon from '@material-ui/icons/Twitter';

import { makeStyles } from "@material-ui/core";
import * as anchor from "@project-serum/anchor";


import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
  shortenAddress,
} from "./candy-machine";
import { fabClasses } from "@mui/material";
import { flexbox } from "@mui/system";

const useStyles = makeStyles(() => ({ 
  typographyStyles: { 
    flex: 2,
    background: '#DFD97D',
    padding: '0 30 px',
  },
  walletStlyes: { 
    background: '#E5C02D',
    height: 40,
    padding: '0 30 px'
  },
  headerOptions: { 
    display: "flex",
    flex: 1,
    justifyContent: "space-evenly"
  },
  MintButtonOptions: {
    background: '#333301',
    primary: '#white',
    secondary: 'black',
  }
  }
));


const ConnectButton = styled(WalletDialogButton)``;

const ConnectedDisplay = styled(WalletDialogButton)``;

const CounterText = styled.span``; // add your styles here

const MintContainer = styled.div`
color: black;
align: right`; // add your styles here

const MintButton = styled(Button)``; // add your styles here

export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT

  const [itemsAvailable, setItemsAvailable] = useState(0);
  const [itemsRedeemed, setItemsRedeemed] = useState(0);
  const [itemsRemaining, setItemsRemaining] = useState(0);

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();

  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const {
        candyMachine,
        goLiveDate,
        itemsAvailable,
        itemsRemaining,
        itemsRedeemed,
      } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );

      setItemsAvailable(itemsAvailable);
      setItemsRemaining(itemsRemaining);
      setItemsRedeemed(itemsRedeemed);

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  };

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(
          candyMachine,
          props.config,
          wallet.publicKey,
          props.treasury
        );

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  useEffect(refreshCandyMachineState, [
    wallet,
    props.candyMachineId,
    props.connection,
  ]);

  const classes = useStyles();
  return (
    <main>
          {/*!wallet ? (
          <ConnectButton>Connect Wallet</ConnectButton>
        ) : ( 
          wallet && ( <p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p>)
          
        )*/}
      {/*wallet && ( <p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p>)}
      
      {wallet && <p>Balance: {(balance || 0).toLocaleString()} SOL</p>*/} 

      {/*wallet && <p>Total made: {itemsAvailable}</p>*/}    
           


    <div data-collapse="medium" data-animation="default" data-duration="400" af-el="NavBar" data-easing="ease" data-easing2="ease" role="banner" className="navigation w-nav">
    <div className="navigation-wrap">
      <a href="index.html" aria-current="page" className="logo-link w-nav-brand w--current"><img src="assets/images/bee_logo.png" loading="lazy" width="50" sizes="49.9921875px" srcSet="images/bee_logo-p-500.png 500w, images/bee_logo-p-800.png 800w, assets/images/bee_logo-p-1080.png 1080w, images/bee_logo.png 1158w" alt=""/></a>
      <div className="menu">
        <div className="menu-button w-nav-button"><img src="/assets/images/menu-icon_1menu-icon.png" width="22" alt="" className="menu-icon"/></div>
        <div className={classes.headerOptions}>
            <div id="twitter_link" >
              <a href="https://www.twitter.com" target="_blank">
               <TwitterIcon fontSize="large"/>
               </a>
            </div>  
      
            </div>


            {!wallet ? (
          <ConnectButton className={classes.walletStlyes}>Connect Wallet</ConnectButton>
        ) : ( 
          wallet && ( <ConnectedDisplay className={classes.walletStlyes}><p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p> </ConnectedDisplay>)
          
        )}
      </div>
    </div>
  </div>
  <div af-el="mint_info" className="section cc-store-home-wrap">
    <div className="container">
      <div className="divider"></div>
      <div className="home-content-wrap">
        <div className="w-layout-grid about-grid">
          <div id="w-node-_86e64837-0616-515b-4568-76c147234d34-b2cc556a">
            <div className="home-section-wrap"><img src="assets/images/beebois.png" loading="lazy" width="427" alt=""/>
              <p className="text-block"><br/> <br/> Â 
              <p>Minted: {itemsAvailable} / {itemsRedeemed}</p> <br/>


          <MintContainer>
          <MintButton className={classes.MintButtonOptions}
            disabled={isSoldOut || isMinting || !isActive}
            onClick={onMint}
            variant="outlined"
          >
            {isSoldOut ? (
              "SOLD OUT"
            ) : isActive ? (
              isMinting ? (
                <CircularProgress />
              ) : (
                "MINT"
              )
            ) : (
              <Countdown
                date={startDate}
                onMount={({ completed }) => completed && setIsActive(true)}
                onComplete={() => setIsActive(true)}
                renderer={renderCounter}
              />

            )} 
          </MintButton>
          </MintContainer>

          </p>
            </div>
          </div>
          <div><img src="assets/images/flower_frame_1.png" loading="lazy" width="503" sizes="(max-width: 767px) 94vw, 502.9921875px" srcSet="images/flower_frame_1-p-500.png 500w, assets/images/flower_frame_1-p-800.png 800w, images/flower_frame_1.png 975w" alt=""/></div>
        </div>
      </div>
    </div>
  </div>
  <div id="Missions" af-el="missions" className="section">
    <div className="container">
      <div className="blog-heading">
        <h2 className="work-heading">Missions We support</h2>
        <div className="w-layout-grid grid-2">
          <h4><strong>The Bee Girl Organization</strong></h4>
          <h4><strong>BEES for the World</strong></h4>
          <h4 className="heading-2">Environmental Defense Fund</h4>
          <p>Sustainable supply chains for high quality organic-certified bee products all over the world. Their commitment is to Biodiversity, Education, Empowerment, and Sustainability (BEES).<br/></p>
          <p>Engages communities across the globe, providing educational classNamees and resources for regenerative beekeeping</p>
          <div>Protecting natural systems, and improving policies that affect these systems, dealing with issues of climate change, public health, sustainable agriculture and ocean protection.<br/></div>
        </div>
      </div>
    </div>
  </div>
  <div id="Hive" className="section-2 wf-section">
    <h2 className="work-heading">The Hive</h2>
    <div id="Loop_Image_Outer" className="div-block-2">
      <div id="Loop_Image_Inner" data-w-id="faaa3bfe-4f26-8b7b-8bb5-e32f58023b05" data-size="-webkit-transform:translate3d(0vw, 0, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);-moz-transform:translate3d(0vw, 0, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);-ms-transform:translate3d(0vw, 0, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);transform:translate3d(0vw, 0, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0)" className="div-block">
      <img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/>
      <img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/>
      <img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/><img src="assets/images/DP.png" width="316" alt="" className="image"/>
      </div>
    </div>
  </div>
  <div id="Roadmap" className="wf-section">
    <h2 className="work-heading">Road Map</h2>
    <div className="div-block-4">
      <div className="w-row">
        <div className="w-col w-col-3"></div>
        <div className="w-col w-col-3"></div>
        <div className="w-col w-col-3"></div>
        <div className="w-col w-col-3"><img src="assets/images/hive_standalone.png" loading="lazy" sizes="(max-width: 767px) 96vw, 23vw" srcSet="assets/images/hive_standalone.png 500w, images/hive_standalone.png 553w" alt=""/></div>
      </div>
    </div>
  </div>
  <div id="FAQ" className="wf-section">
    <div>
      <h1 className="work-heading">fAQ</h1>
    </div>
  </div>
  <div af-el="footer" className="section">
    <div className="container">
      <div className="footer-wrap">
        <a href="https://webflow.com/" target="_blank" className="webflow-link w-inline-block">
          <div className="paragraph-tiny">Bee Bois</div>
        </a>
      </div>
    </div>
  </div>
  <script src="https://d3e54v103j8qbb.cloudfront.net/js/jquery-3.5.1.min.dc5e7f18c8.js?site=61664c3367df91bca0cc5567" type="text/javascript" integrity="sha256-9/aliU8dGd2tb6OSsuzixeV4y/faTqgFtohetphbbj0=" crossOrigin="anonymous"></script>
  <script src="js/webflow.js" type="text/javascript"></script>  
        
      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>
    
    </main>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  return (
    <CounterText>
      {hours + (days || 0) * 24} hours, {minutes} minutes, {seconds} seconds
    </CounterText>
  );
};

export default Home;
